.basket {
    margin-bottom: rem(90px);
}

.basket__title {
    font-weight: 500;
    font-size: rem(32px);
    line-height: rem(40px);
    margin-bottom: rem(32px);
}

.header-basket__item {
    &--product {
        flex: 0 0 57%;
    }

    &--quantity {
        flex: 0 0 12%;

        @include tablets() {
            flex: 0 0 14%;
        }
    }
}

.basket__content {
    display: flex;
    justify-content: space-between;

    @include tablets() {
        flex-direction: column;
    }
}

.basket__total {
    flex: 0 0 60%;

    @include tablets () {
        margin-bottom: rem(29px);
    }
}

.basket__check {
    flex: 0 0 23%;

    @include tablets() {
        text-align: center;
    }
}

.header-basket {
    display: flex;
    font-size: rem(17px);
    line-height: rem(28px);
    color: rgba(0, 0, 0, 0.4);
    justify-content: space-between;
    padding-bottom: rem(4px);
    margin-bottom: rem(20px);
    border-bottom: rem(1px) solid rgba(0, 0, 0, 0.15);

    @include phones() {
        font-size: 0;
    }
}

.basket__list {
    margin-bottom: rem(27px);
}

.basket__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: rem(20px) 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.15);

    @include phones() {
        align-items: flex-start;
        flex-direction: column;
        justify-content: center;
    }
}

.basket__product {
    display: flex;
    align-items: center;

    @include phones() {
        margin-bottom: rem(20px);
    }
}

.basket__img {
    margin-right: rem(24px);
}

.basket__text-title {
    line-height: rem(26px);
    margin-bottom: rem(4px);
    padding-right: rem(5px);
}

.basket__remove {
    display: block;
    font-size: rem(17px);
    line-height: rem(20px);
    text-decoration-line: underline;
    color: $brown;

    &:hover {
        text-decoration: none;
    }
}

.basket__price {
    line-height: rem(26px);
    font-weight: 500;
    white-space: nowrap;

    @include phones() {
        display: flex;
        justify-content: flex-end;
        width: 100%;
    }
}

.basket__total-continue {
    font-weight: 500;
    font-size: rem(20px);
    line-height: rem(25px);
    text-decoration-line: underline;
    color: $brown;

    &:hover {
        text-decoration: none;
    }
}

.check-basket__title {
    display: block;
    font-weight: 500;
    line-height: rem(28px);
    margin-bottom: rem(6px);
}

.btn-basket {
    white-space: nowrap;
}