.product__bar {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    visibility: hidden;
    transition: all .5s ease;
}

.product__bar-content {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    padding-bottom: 5%;

    &::before {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #FFFFFF;
        opacity: 0.7;
    }
}

.sizes {
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    position: relative;
    font-size: rem(17px);
    line-height: rem(19px);
    color: $black;
}

.product__bar-btn {
    position: relative;
    z-index: 1000;
    margin-bottom: rem(10px);
}


.sizes__link:hover {
    color: $brown;
}