.container-error {
    position: relative;
    height: 100vh;
}

.error {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.error__main {
    position: relative;
    margin-bottom: rem(33px);
}

.error__img {
    width: rem(370px);
    height: rem(443px);
    top: 50%;
    left: 50%;

    @include phones() {
        width: rem(206px);
        height: rem(246px);
    }
}

.error__title {
    position: absolute;
    top: 70%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-weight: 500;
    font-size: rem(100px);
    line-height: rem(117px);
    color: $brown;
    margin: 0;

    @include phones() {
        font-size: rem(57px);
    }
}

.error__text {
    text-align: center;

    span {
        display: block;
        line-height: rem(25px);
        white-space: nowrap;
    }
}

.error__subtitle {
    font-weight: 500;
    font-size: rem(44px);
    line-height: rem(51px);
    margin-bottom: rem(25px);
}