.banner {
    margin-bottom: rem(67px);
}

.banner__wrap {
    position: relative;
    background: url("../img/main/slider.png") center center/ cover no-repeat;
    min-height: rem(650px);
    color: #FFFFFF;
    position: relative;

    &::before {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(90deg, #000000 0%, rgba(0, 0, 0, 0.2) 100%);
        opacity: 0.8;
    }
}

.banner__wrap--video {
    background: url("../img/main/banner.png") left center/ cover no-repeat;
    background-position: left 70% center;
    min-height: rem(400px);
}

.banner__title {
    font-weight: 500;
    font-size: rem(44px);
    line-height: rem(55px);
    margin-bottom: rem(31px);

    &--video {
        margin-bottom: rem(26px);
    }
}

.banner__text {
    margin-bottom: rem(34px);
    display: block;
    line-height: rem(26px);

    &--video {
        margin-bottom: rem(3px);
        opacity: .6;
    }
}

.banner__desc {
    position: relative;
    padding-top: rem(190px);
    width: 40%;
    padding-bottom: rem(60px);

    &--video {
        padding-top: rem(138px);
    }

    @include tablets() {
        padding-top: rem(150px);
        width: 65%;
    }

    @include phones() {
        width: 85%;
    }
}

// .slider__list

.slider {
    position: relative;
}

.slick-track {
    display: flex;
}

.slick-list {
    overflow: hidden;
}

.slick-list .slick-track {
    display: flex;
    align-items: flex-start;
}

.slider__list .slick-dots {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: 5%;
    left: 50%;
    transform: translateX(-50%);
}

.slider__list .slick-dots li {
    margin-right: rem(10px);
    display: flex;

    &:last-child {
        margin-right: 0;
    }
}

.slider__list .slick-dots button {
    font-size: 0;
    width: rem(10px);
    height: rem(10px);
    background: #fff;
    opacity: 0.5;
    border: none;
    outline: none;
    cursor: pointer;
}

.slider__list .slick-dots .slick-active {
    background-color: $darkBrown;
}

.slider__item {
    height: 100%;
}