.burger {
    width: 20px;
    height: 12px;
    cursor: pointer;
    z-index: 20000;
    position: relative;
    margin-right: rem(13px);
    display: none;

    @include tablets() {
        display: block;
    }
}

.navigation-active .burger__line {

    &--first {
        transform: rotate(45deg);
        top: 5px;
    }

    &--second {
        transform: scale(0);
    }

    &--third {
        transform: rotate(-45deg);
        top: 5px;
    }
}

.burger__line {
    display: block;
    width: 100%;
    height: 2px;
    background-color: #000;
    position: absolute;
    left: 0;
    transition: all .3s ease;

    &--first {
        top: 0;
    }

    &--second {
        top: 50%;
        transform: translateY(-50%);
    }

    &--third {
        bottom: 0
    }
}
