.footer__header {
    min-height: rem(120px);
    background: #EDECEA;
    display: flex;
    justify-content: center;
    align-items: center;

    @include tablets() {
        height: auto;
    }
}

.advantages {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    justify-items: center;
    justify-content: space-around;
    padding: rem(15px) 0;

    @include tablets() {
        grid-gap: 0 rem(65px);
        padding: 0;
    }
}

.advantages__item {
    grid-column: span 1;
    width: 100%;
    background-color: #EDECEA;
    padding: rem(10px);
    border-right: 1px solid rgba(0, 0, 0, 0.15);

    &:last-child {
        margin-right: -1px;
        border: none;
    }

    &:nth-child(3) {
        margin-right: -1px;
    }

    @include tablets() {
        padding: rem(28px) rem(15px);
        grid-column: span 2;
        border-right: none;
        border-bottom: 1px solid rgba(0, 0, 0, 0.15);
    }

    @include phones() {
        grid-column: span 4;
        padding: rem(20px) rem(7px);
    }
}

.advantages__desc {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    flex-wrap: wrap;

    &:last-child::after {
        border: none;
    }
}

.advantages__img {
    width: rem(40px);
    height: rem(40px);
    margin-right: rem(36px);

    @include tablets() {
        margin-right: rem(30px);
    }
}

.advantages__icon {
    width: 100%;
    height: 100%;
}

.advantages__text {
    display: flex;
    flex-direction: column;
}

.advantages__title {
    font-weight: 500;
    font-size: rem(17px);
    line-height: rem(30px);
}

.advantages__subtitle {
    font-weight: normal;
    color: $black;
    font-size: rem(17px);
    line-height: rem(30px);
}