.styleguide {
    margin-bottom: rem(67px);
}

.styleguide__list {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    @include phones() {
        flex-direction: column;
    }
}

.styleguide__item {
    background: url("../img//main/styleguide1.png") center center / cover no-repeat;
    flex: 1;
    position: relative;
    min-height: rem(240px);
    margin-right: rem(25px);

    &--shoes {
        background-image: url("../img//main/styleguide1.png");
    }

    &--wear {
        background-image: url("../img//main/styleguide2.png");
    }

    &:last-child {
        margin-right: 0;
    }

    @include tablets() {
        margin-right: rem(20px);
    }

    @include phones() {
        width: 100%;
        margin-right: 0;
        margin-bottom: rem(20px);

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.styleguide__img {
    width: 100%;
    height: 100%;
}

.styleguide__desc {
    width: 65%;
    padding: rem(38px);

    @include tablets() {
        width: 100%;
    }

}

.details {
    color: $brown;
    text-decoration: underline;
    font-weight: 500;
    font-size: rem(20px);
    line-height: rem(25px);

    &:hover {
        transition: color .2s ease;
        text-decoration: none;
        color: $darkBrown;
    }
}

.slick-list {
    width: 100%;
}

.product__list {
    width: 100%;
}

.product__img {
    width: 100%;
    height: 100%;
}