html {
  font-size: 22px;
}

html,
body {
  height: 100%;
}

body {
  font-family: 'Inter', Arial, Helvetica, sans-serif;
  line-height: 1.42;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  color: #000000;
}

.lock {
  overflow: hidden;
}

.wrapper {
  position: relative;
  width: 100%;
  min-height: 29.54545rem;
  overflow: hidden;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-weight: normal;
}

.tabel {
  border-collapse: collapse;
}

ul {
  padding: 0;
  margin: 0;
}

ul li {
  list-style: none;
}

p {
  padding: 0;
  margin: 0;
}

footer {
  position: relative;
  width: 100%;
}

* {
  box-sizing: border-box;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

input {
  outline: none;
}

.header__contacts {
  display: flex;
  justify-content: space-between;
  padding: 0.36364rem 0;
  border-bottom: 0.04545rem solid rgba(0, 0, 0, 0.15);
}

.address {
  font-size: 0.77273rem;
  display: flex;
}

.address__item {
  margin-right: 1.5rem;
}

.address__item:last-child {
  margin-right: 0;
}

.address__link {
  color: rgba(0, 0, 0, 0.4);
}

.address__link span {
  color: #000;
  transition: all .2s ease;
}

.address__link:hover span {
  color: #DBA674;
}

.socials {
  display: flex;
  align-items: center;
}

.socials__item {
  margin-right: 0.68182rem;
}

.socials__item:last-child {
  margin-right: 0;
}

.socials__link {
  display: flex;
  width: 0.63636rem;
  height: 0.63636rem;
}

.socials__link:hover .socials__icon {
  transition: all .2s ease;
  fill: #DBA674;
}

.socials__icon {
  width: 100%;
  height: 100%;
  fill: rgba(0, 0, 0, 0.4);
}

.bar {
  display: flex;
}

.bar__item {
  margin-right: 1.36364rem;
}

.bar__item:last-child {
  margin-right: 0;
}

.bar__icon {
  width: 0.90909rem;
  height: 0.90909rem;
}

.bar__link {
  position: relative;
}

.bar__link:hover {
  transition: all .3s ease;
  fill: #CE9B6A;
}

.number {
  width: 0.68182rem;
  height: 0.68182rem;
  background-color: #DBA674;
  border-radius: 50%;
  position: absolute;
  right: -0.22727rem;
  bottom: 0;
}

.number::after {
  content: attr(data-number);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-weight: 500;
  font-size: 0.5rem;
  line-height: 0.63636rem;
  color: #fff;
}

.address--burger {
  display: none;
}

.socials--burger {
  display: none;
}

.logo {
  margin-right: 0.68182rem;
}

.header__navigation {
  padding: 0.90909rem 0 1.36364rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navigation {
  font-weight: 500;
  font-size: 0.90909rem;
  transition: transform .5s ease;
}

.navigation__link:hover {
  transition: all .2s ease;
  color: #DBA674;
  fill: #DBA674;
}

.navigation__link:hover .arrow {
  transition: all .2s ease;
  fill: #DBA674;
}

.navigation__list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.navigation__item {
  margin-right: 1.36364rem;
}

.navigation__item:last-child {
  margin-right: 0;
}

.header__nav-list {
  margin-right: 0.68182rem;
}

.dropdown:hover {
  fill: #DBA674;
}

.arrow {
  width: 0.45455rem;
  height: 0.45455rem;
  fill: #000;
}

.arrow:hover {
  fill: #DBA674;
}

.dropdown-link {
  position: relative;
}

.dropdown {
  position: absolute;
  width: 100%;
  top: 100%;
  left: 0;
  display: none;
  background-color: #fff;
  z-index: 1000;
}

.dropdown__list {
  display: flex;
  justify-content: space-between;
  padding: 0.63636rem;
}

.header {
  position: relative;
}

.dropdown__item {
  font-weight: 500;
  font-size: 0.90909rem;
  line-height: 1.04545rem;
  margin-bottom: 0.27273rem;
}

.product-category {
  font-size: 0.77273rem;
  line-height: 1.45455rem;
  font-weight: normal;
}

.product-category__link:hover {
  transition: all .2s ease;
  color: #DBA674;
}

.active-dropdown {
  transition: transform .2s ease;
  transform: rotate(180deg);
}

.navigation__icon {
  transition: transform .2s ease;
}

.banner {
  margin-bottom: 3.04545rem;
}

.banner__wrap {
  position: relative;
  background: url("../img/main/slider.png") center center/cover no-repeat;
  min-height: 29.54545rem;
  color: #FFFFFF;
  position: relative;
}

.banner__wrap::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, #000000 0%, rgba(0, 0, 0, 0.2) 100%);
  opacity: 0.8;
}

.banner__wrap--video {
  background: url("../img/main/banner.png") left center/cover no-repeat;
  background-position: left 70% center;
  min-height: 18.18182rem;
}

.banner__title {
  font-weight: 500;
  font-size: 2rem;
  line-height: 2.5rem;
  margin-bottom: 1.40909rem;
}

.banner__title--video {
  margin-bottom: 1.18182rem;
}

.banner__text {
  margin-bottom: 1.54545rem;
  display: block;
  line-height: 1.18182rem;
}

.banner__text--video {
  margin-bottom: 0.13636rem;
  opacity: .6;
}

.banner__desc {
  position: relative;
  padding-top: 8.63636rem;
  width: 40%;
  padding-bottom: 2.72727rem;
}

.banner__desc--video {
  padding-top: 6.27273rem;
}

.slider {
  position: relative;
}

.slick-track {
  display: flex;
}

.slick-list {
  overflow: hidden;
}

.slick-list .slick-track {
  display: flex;
  align-items: flex-start;
}

.slider__list .slick-dots {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);
}

.slider__list .slick-dots li {
  margin-right: 0.45455rem;
  display: flex;
}

.slider__list .slick-dots li:last-child {
  margin-right: 0;
}

.slider__list .slick-dots button {
  font-size: 0;
  width: 0.45455rem;
  height: 0.45455rem;
  background: #fff;
  opacity: 0.5;
  border: none;
  outline: none;
  cursor: pointer;
}

.slider__list .slick-dots .slick-active {
  background-color: #CE9B6A;
}

.slider__item {
  height: 100%;
}

.rating {
  display: flex;
  line-height: 1;
  margin-bottom: 0.27273rem;
}

.rating__list {
  display: flex;
  flex-direction: row-reverse;
}

.rating__item {
  position: relative;
  color: #ECEBE9;
}

.rating__item::before {
  content: "â˜…";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.checked,
.checked ~ .rating__item::before {
  color: #DBA674;
}

.product__bar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  transition: all .5s ease;
}

.product__bar-content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  padding-bottom: 5%;
}

.product__bar-content::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #FFFFFF;
  opacity: 0.7;
}

.sizes {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
  position: relative;
  font-size: 0.77273rem;
  line-height: 0.86364rem;
  color: rgba(0, 0, 0, 0.4);
}

.product__bar-btn {
  position: relative;
  z-index: 1000;
  margin-bottom: 0.45455rem;
}

.sizes__link:hover {
  color: #DBA674;
}

.product {
  margin-bottom: 2.95455rem;
}

.product__list {
  display: flex;
  justify-content: center;
}

.product__item {
  margin-right: 0.59091rem;
  margin-left: 0.59091rem;
  outline: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.product__item:last-child {
  margin-right: 0;
}

.product__title {
  font-style: normal;
  font-weight: normal;
  font-size: 0.81818rem;
  line-height: 1.18182rem;
  margin-bottom: 0.09091rem;
}

.product__link {
  width: 100%;
  height: 100%;
  display: block;
}

.discount {
  text-decoration: line-through;
  font-size: 0.77273rem;
  line-height: 0.90909rem;
  color: #A6A6A6;
}

.product__price-text {
  font-weight: 500;
  line-height: 1.27273rem;
}

.product__img {
  position: relative;
}

.product__img:hover .product__bar {
  opacity: 1;
  visibility: visible;
}

.product__desc {
  padding-top: 0.68182rem;
  display: flex;
  flex-direction: column;
}

.slick-track {
  align-items: baseline;
}

.product__list,
.product__list .slick-list {
  position: relative;
  min-width: 0;
  overflow: hidden;
  display: flex;
}

.product__list .slick-arrow {
  position: absolute;
  top: 30%;
  z-index: 10000;
  font-size: 0;
  width: 2.27273rem;
  height: 2.27273rem;
  background: #000000;
  opacity: 0.3;
  border: none;
  transform: translateY(-50%);
}

.product__list .slick-arrow::after {
  content: "";
  width: 0.45455rem;
  height: 0.27273rem;
  display: block;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
