.footer__content {
    min-height: rem(422px);
    background-color: #000;
    color: #fff;
}

.container-footer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer__title {
    margin-top: rem(67px);
    font-weight: 500;
    font-size: rem(32px);
    line-height: rem(40px);
    margin-bottom: rem(9px);
}

.footer__subtitle {
    display: block;
    line-height: rem(30px);
    color: rgba(255, 255, 255, 0.6);
    margin-bottom: rem(30px);
    text-align: center;
}

.form {
    position: relative;
    margin-bottom: rem(40px);

    @include tablets() {
        width: 85%;
    }
}

.input__error {
    border: rem(2px) solid #ff0000;
}

.form__email {
    width: rem(611px);
    height: rem(50px);

    @include tablets() {
        width: 100%;
    }
}

.copyright {
    display: block;
    margin-top: rem(34px);
    font-size: rem(17px);
    line-height: rem(30px);
    color: rgba(255, 255, 255, 0.6);
    margin-bottom: rem(20px);
}