.dropdown-link {
    position: relative;
}

.dropdown__container {
    @include tablets() {
        width: 100%;
        max-width: 100%;
    }
}

.dropdown {
    position: absolute;
    width: 100%;
    top: 100%;
    left: 0;
    display: none;
    background-color: #fff;
    z-index: 1000;

    @include tablets() {
        position: relative;
    }
}

.dropdown__list {
    display: flex;
    justify-content: space-between;
    padding: rem(14px);
    
    @include tablets() {
       flex-wrap: wrap;
    }
}

.header {
    position: relative;
}

.dropdown__item {
    font-weight: 500;
    font-size: rem(20px);
    line-height: rem(23px);
    margin-bottom: rem(6px);

    @include tablets() {
        flex: 0 0 50%;
        padding: rem(38px) 0 rem(25px) 0;
    }
}

.product-category {
    font-size: rem(17px);
    line-height: rem(32px);
    font-weight: normal;

    &__link:hover {
        transition: all .2s ease;
        color: $brown;
    }
}

.active-dropdown {
    transition: transform .2s ease;
    transform: rotate(180deg);
}

.navigation__icon {
    transition: transform .2s ease;
}