.bar {
    display: flex;
}

.bar__item {
    margin-right: rem(30px);

    &:last-child {
        margin-right: 0;
    }
}

.bar__icon {
    width: rem(20px);
    height: rem(20px);
}

.bar__link {
    position: relative;

    &:hover {
        transition: all .3s ease;
        fill: $darkBrown;
    }
}

.number {
    width: rem(15px);
    height: rem(15px);
    background-color: $brown;
    border-radius: 50%;
    position: absolute;
    right: rem(-5px);
    bottom: 0;
}

.number::after {
    content: attr(data-number);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-weight: 500;
    font-size: rem(11px);
    line-height: rem(14px);
    color: #fff;
}

.address--burger {
    display: none;

    @include tablets() {
        display: flex;
        width: 95%;
        max-width: rem(1200px);
        margin: 0 auto;
        margin-top: rem(40px);
        margin-bottom: rem(47px);
    }
}

.socials--burger {
    display: none;

    @include tablets() {
        display: flex;
        width: 95%;
        max-width: rem(1200px);
        margin: 0 auto;
    }

}