.category {
    margin-bottom: rem(100px);
}

.category__title {
    margin-bottom: rem(39px);
    text-align: start;
}

.category__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: rem(18px)
}

.category__item {
    grid-column: span 2;
    background: #EDECEA;
    min-height: rem(100px);
    transition: transform .3s ease;

    &:hover {
        transform: translateY(-5px);
    }

    @include tablets() {
        grid-column: span 3;
    }

    @include phones() {
        grid-column: span 6;
    }
}

.category__link {
    display: flex;
    align-items: center;
    height: 100%;

    &:hover {
        transition: all .3s ease;
        color: $darkBrown;
    }
}

.category__img {
    margin-right: rem(10px);
    width: 34%;
}

.category__name {
    font-size: $baseFontSize;
    font-weight: 500;
    line-height: rem(28px);
    margin: 0;
}