.tabs-card__list {
    display: flex;
    line-height: rem(30px);
    font-weight: 500;
    flex-wrap: wrap;
    margin-bottom: rem(22px);
    font-size: rem(20px);
}

.tabs-card__item {
    border-bottom: 1px solid rgba(0, 0, 0, 0.15);
    position: relative;

    &:last-child {
        padding-right: rem(30px);
    }

    &-active {
        border-bottom: 3px solid $brown;
    }
}

.tabs-card__link {
    position: relative;
    padding-right: rem(15px);
    padding-bottom: rem(4px);
    display: block;

    &::after {
        content: attr(data-quantity);
        position: absolute;
        top: 55%;
        left: 100%;
        transform: translateY(-55%);
        color: rgba(0, 0, 0, 0.4);
        line-height: rem(30px);
    }
}

.tabs-card__text span {
    line-height: rem(26px);
    color: rgba(0, 0, 0, 0.4);
}

.tabs-card__title {
    font-weight: 500;
    line-height: rem(30px);
    margin-bottom: rem(7px);
}

.tabs-card__content {
    position: relative;
    overflow: hidden;
}

.tabs-card__text {
    transition: all .4s ease;
    opacity: 1;
    visibility: visible;
}

.tabs-hiden {
    transform: translateY(-147%);
    height: 0;
    opacity: 0;
    visibility: hidden;
}