.card-product {
    margin-bottom: rem(67px);
}

.card-product__container {
    @include tablets() {
        width: 95%;
        max-width: rem(520px);
        margin: 0 auto;
    }
}

.card-product__content {
    display: flex;
    justify-content: space-between;

    @include tablets() {
        flex-direction: column;
    }
}

.card-product__list {
    min-width: 0;
}

.card-product__column {
    flex: 0 0 49%;
    min-width: 0;

    &:last-child {
        margin-right: 0;
    }
}

.desc-card {
    flex: 0 0 41%;
}

.card-product__list {
    margin-bottom: rem(30px);
}

.card-bg__item {
    display: flex;
    justify-content: center;
}

.card-sm__item {
    display: flex;
    min-width: 0;
    justify-content: center;
    transition: all .1s ease;
    min-width: rem(150px);
    width: 100%;
}

.card-sm__item.slick-center {
    opacity: 1;
}

.card-sm__img {
    width: 90%;
}

.card-bg__img {
    max-height: rem(640px);

    @include tablets() {
        max-height: rem(630px);
    }

    @include phones() {
        max-height: rem(500px);
    }
}

.card-sm {
    position: relative;
    padding: 0 rem(5px);
    max-width: rem(480px);
    margin: 0 auto;

    @include tablets() {
        margin-bottom: rem(21px);

    }

    .slick-arrow {
        position: absolute;
        top: 50%;
        z-index: 100;
        font-size: 0;
        cursor: pointer;
    }

    .slick-prev {
        left: 0;
    }

    .slick-next {
        right: 0;
    }
}