.filter__size-input {
    display: none;
}

.filter__size-input:checked+.filter__size::after {
    transform: scale(1);
}

.filter__size {
    font-weight: 500;
    font-size: rem(17px);
    line-height: rem(19px);
    display: inline-flex;
    align-items: center;
    position: relative;
    cursor: pointer;

    &::before {
        content: "";
        flex: 0 0 18px;
        align-self: flex-start;
        height: 18px;
        background-color: #fff;
        border-radius: 3px;
        border: 1px solid #D9D9D9;
        margin-right: 10px;
    }

    &::after {
        transition: .3s all ease;
        content: "";
        position: absolute;
        top: 5px;
        left: 5px;
        width: 10px;
        height: 10px;
        background-color: $brown;
        border-radius: 2px;
        transform: scale(0);
    }
}