.btn {
    padding: rem(15px) rem(80px);
    background-color: $brown;
    border: none;
    color: #fff;
    font-weight: 500;
    font-size: rem(18px);
    line-height: rem(23px);
    cursor: pointer;

    &:hover {
        transition: background-color .4s ease;
        background-color: $darkBrown;
    }
}

.button--product {
    padding: rem(10px) rem(50px) !important;
}

.btn--sorting {
    padding: rem(10px) rem(40px);
}

.form__btn {
    width: rem(50px);
    height: rem(50px);
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $brown;
    border: none;
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;

    &:hover {
        transition: all .2s ease;
        background-color: $darkBrown;
    }

    &-icon {
        width: rem(22px);
        height: rem(22px);
    }
}