.form-card {
    margin-bottom: rem(35px);
}

.form-card__item {
    display: flex;
    align-items: center;
    margin-bottom: rem(19px);

    &:last-child {
        margin-bottom: 0;
    }
}

.form-card__label-color {
    line-height: rem(26px);
    margin-right: rem(15px);
}

.options-card {
    display: flex;
}

.options-card__radio {
    display: none;
}

.options-card__radio:checked+.form-card__label::after {
    transform: scale(1);
}


.form-card__label {
    display: flex;
    position: relative;
    cursor: pointer;

    &::before {
        content: '';
        height: rem(22px);
        width: rem(22px);
        margin-right: rem(14px);
    }

    &::after {
        transition: all .3s ease;
        content: '';
        border-bottom: 2px solid $brown;
        position: absolute;
        top: 120%;
        left: 0;
        right: 0;
        width: rem(22px);
        transform: scale(0);
    }

    &--1::before {
        background: #C4C4C4;
    }

    &--2::before {
        background: #484848;
    }

    &--3::before {
        background: #5A4570;
    }
}

.form-card__size {
    margin-right: rem(26px);
}

.options-card__radio:checked+.form-card__label-size span::after {
    transform: scale(1);
}

.form-card__label-size {
    display: flex;
    position: relative;
    cursor: pointer;
    margin-right: rem(10px);
    padding: 0 rem(3px);

    span {
        line-height: rem(26px);
        color: rgba(0, 0, 0, 0.4);
        display: flex;

        &::after {
            transition: all .3s ease;
            content: '';
            border-bottom: 2px solid $brown;
            position: absolute;
            top: 120%;
            left: 0;
            right: 0;
            transform: scale(0);
        }
    }
}

.counter {
    display: inline-flex;
    margin-right: rem(15px);
}

.counter__area {
    display: flex;
}

.counter__controls {
    display: flex;
    flex-direction: column;
}

.counter__input {
    width: rem(71px);
    height: rem(52px);
    background: #ECEBE9;
    border: none;
    margin-right: rem(2px);
    padding: rem(20px) rem(20px);
    text-align: center;
}

.counter__btn {
    width: rem(24px);
    height: rem(24px);
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ECEBE9;
    color: rgba(0, 0, 0, 0.4);
    line-height: rem(25px);
    border: none;
    margin-bottom: rem(4px);
    cursor: pointer;
    outline: none;

    &:last-child {
        margin-bottom: 0;
    }
}

.form-card__btn {
    display: inline-flex;
}