.section-info {
    margin-bottom: rem(74px);
    position: relative;
}

.news {
    display: flex;
}

.news__item {
    margin-right: rem(12px);
    margin-left: rem(12px);

    &:last-child {
        margin-right: 0;
    }
}

.news__link {
    display: block;
    margin-bottom: rem(27px);
}

.news__img {
    width: 100%;
    height: 100%;
}

.news__desc {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.news__date {
    line-height: rem(20px);
    color: $black;
    margin-bottom: rem(14px);
    display: block;
}

.news__title {
    font-weight: 500;
    line-height: rem(30px);
    display: block;
    margin-bottom: rem(10px);
}

.news__text {
    line-height: rem(28px);
    color: $black;
    margin-bottom: rem(21px);
    font-size: rem(17px);
}

// 
.section-info__news,
.section-info__news .slick-list {
    position: relative;
    min-width: 0;
    overflow: hidden;
    display: flex;
}

.section-info .slick-arrow {
    position: absolute;
    top: 30%;
    z-index: 10000;
    font-size: 0;
    width: rem(50px);
    height: rem(50px);
    background: #000000;
    opacity: 0.3;
    border: none;
    transform: translateY(-50%);

    &::after {
        content: "";
        width: rem(10px);
        height: rem(6px);
        display: block;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        position: absolute;
        background-image: url("data:image/svg+xml;utf8,<svg viewBox='0 0 11 7' xmlns='http://www.w3.org/2000/svg'><path d='M9.85975 1.54027C10.0597 1.35027 10.0597 1.04027 9.85975 0.860273L9.13978 0.180281C8.93978 -0.00971924 8.61975 -0.00971924 8.42975 0.180281L5.02976 3.47026L1.59977 0.150312C1.39977 -0.0396875 1.07977 -0.0396875 0.879767 0.150312L0.159766 0.830305C-0.0402344 1.02031 -0.0402344 1.3203 0.159766 1.5103L4.66978 5.86027C4.86978 6.05027 5.18978 6.05027 5.38978 5.86027L9.85975 1.54027Z' fill='%23ffffff'/></svg>");
        opacity: 1;
        background-position: center center;
        background-repeat: no-repeat;
        background-color: transparent;
        border-radius: 0;
    }
}

.section-info .slick-prev {
    left: 0;
    transform: rotate(90deg);
}

.section-info .slick-next {
    right: 0;
    transform: rotate(-90deg);
}

.new-product {
    min-width: 0;
}

.news__item {
    min-width: 0;
}