.blog {
    margin-bottom: rem(74px);
}

.blog__news {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.blog__item {
    margin-bottom: rem(50px);
    min-width: 0;
    margin-left: 0;
    width: 32.222% !important;

    @include tablets() {
        width: 48% !important;
    }

    @include phones() {
        width: 100% !important;
        max-width: 100%;
    }
}

.blog__news,
.blog__news .slick-list {
    position: relative;
    min-width: 0;
    overflow: hidden;
    display: flex;
}

.catalog__product,
.catalog__product .slick-list {
    position: relative;
    min-width: 0;
    overflow: hidden;
    display: flex;
}

.controls-slider__arrow .slick-arrow {
    position: relative;
    z-index: 10000;
    font-size: 0;
    width: rem(16px);
    height: rem(16px);
    border: none;
    outline: none;
    cursor: pointer;
}

.controls-slider__arrow {
    .slick-next::after {
        content: "";
        width: rem(16px);
        height: rem(16px);
        display: block;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        position: absolute;
        background-image: url("data:image/svg+xml;utf8,<svg viewBox='0 0 16 16' xmlns='http://www.w3.org/2000/svg'><path d='M1 8.99997H12.17L7.29 13.88C6.9 14.27 6.9 14.91 7.29 15.3C7.68 15.69 8.31 15.69 8.7 15.3L15.29 8.70997C15.68 8.31997 15.68 7.68997 15.29 7.29997L8.71 0.699971C8.32 0.309971 7.69 0.309971 7.3 0.699971C6.91 1.08997 6.91 1.71997 7.3 2.10997L12.17 6.99997H1C0.45 6.99997 0 7.44997 0 7.99997C0 8.54997 0.45 8.99997 1 8.99997Z'/></svg>");
        opacity: 1;
        background-position: center center;
        background-repeat: no-repeat;
        background-color: #fff;
        border-radius: 0;
        fill: #000;
    }

    .slick-prev::after {
        content: "";
        width: rem(16px);
        height: rem(16px);
        display: block;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        position: absolute;
        opacity: 1;
        background-position: center center;
        background-repeat: no-repeat;
        background-color: #fff;
        border-radius: 0;
        fill: #000;
        background-image: url("data:image/svg+xml;utf8,<svg viewBox='0 0 16 16' xmlns='http://www.w3.org/2000/svg'><path d='M15 7.00003L3.83 7.00003L8.71 2.12003C9.1 1.73003 9.1 1.09003 8.71 0.700029C8.32 0.310028 7.69 0.310028 7.3 0.700029L0.710001 7.29003C0.32 7.68003 0.32 8.31003 0.710001 8.70003L7.29 15.3C7.68 15.69 8.31 15.69 8.7 15.3C9.09 14.91 9.09 14.28 8.7 13.89L3.83 9.00003L15 9.00003C15.55 9.00003 16 8.55003 16 8.00003C16 7.45003 15.55 7.00003 15 7.00003Z'/></svg>");
    }
}

.controls-slider .slick-arrow {
    position: absolute;
}

.controls-slider .slick-dots {
    display: inline-flex;
    max-width: rem(200px);
    flex-wrap: wrap;

    li button {
        font-weight: 500;
        font-size: rem(17px);
        line-height: rem(30px);
        border: none;
        background: #fff;
        outline: none;
        cursor: pointer;
    }

    .slick-active button {
        transition: color .3s ease;
        color: $brown;
    }
}

.presentation {
    border: none;
}

.controls-slider {
    display: flex;
    justify-content: center;
    align-items: center;
}

.controls-slider__arrow {
    position: relative;
    display: initial;

    .slick-arrow {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }

    .slick-next {
        left: 110%;
    }

    .slick-prev {
        right: 110%;
    }
}

.slick-dots__none {
    display: none;
}