.breadcrumb {
    min-height: rem(55px);
    background: #F2F2F0;
    display: flex;
    align-items: center;
    margin-bottom: rem(33px);
}

.breadcrumb__list {
    font-size: rem(15px);
    line-height: rem(18px);
    color: rgba(0, 0, 0, 0.4);
    display: flex;
    flex-wrap: wrap;
}

.breadcrumb__item {
    padding-left: rem(10px);
    padding-right: rem(10px);
    position: relative;

    &:hover {
        transition: all .3s ease;
        color: $darkBrown;

        &::before {
            color: rgba(0, 0, 0, 0.4);
        }
    }

    &::before {
        display: block;
        content: "/";
        position: absolute;
        top: 0;
        right: 0;
    }

    &:last-child::before {
        display: none;
    }
}