.catalog-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: rem(40px);

    @include tablets() {
        align-items: flex-end;
    }
}

.catalog-header__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;

    @include tablets() {
        flex-direction: column;
        align-items: flex-start;
    }
}

.catalog-header__title {
    font-weight: 500;
    font-size: rem(32px);
    line-height: rem(37px);
    margin: 0;

    @include tablets() {
        margin-bottom: rem(16px);
    }
}

.sorting-catalog {
    position: relative;
}

.sorting-catalog__controls {
    position: absolute;
    top: 100%;
    left: 0;
    display: none;
    z-index: 1000;
    background-color: #fff;
    border: 1px solid rgba(0,0,0,.15);
}

.form-sorting {
    padding: rem(16px) rem(10px);
}

.sorting-catalog__link {
    cursor: pointer;

    &:hover {
        transition: all .2s ease;
        color: $brown;

        .arrow {
            transition: all .2s ease;
            fill: $brown;
        }
    }

    span {
        font-size: rem(17px);
        font-weight: 500;
    }
}

.form-sorting__title {
    font-size: rem(17px);
    font-weight: 500;
    margin-bottom: rem(15px);
    line-height: 1;
}

.option__label:hover span {
    transition: all .3s ease;
    color: $darkBrown;
}

.options {
    margin-bottom: rem(20px);
}

.option__radio {
    display: none;
}

.option__radio:checked+.option__label::after {
    transform: scale(1);
}

.option__label {
    font-size: rem(16px);
    display: inline-flex;
    line-height: 140%;
    align-items: center;
    position: relative;
    cursor: pointer;

    &::before {
        content: "";
        flex: 0 0 22px;
        align-self: flex-start;
        height: 22px;
        background-color: #fff;
        border-radius: 50%;
        border: 1px solid #D9D9D9;
        margin-right: 10px;
    }

    &::after {
        transition: .3s all ease;
        content: "";
        position: absolute;
        top: 4px;
        left: 4px;
        width: 16px;
        height: 16px;
        background-color: $brown;
        border-radius: 50%;
        transform: scale(0);
    }
}

.catalog-header__filter {
    display: none;

    @include tablets() {
        display: block;
    }
}

.catalog-header__link {
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover {
        transition: all .3s ease;
        fill: $brown;
        color: $brown;
    }

    span {
        font-weight: 500;
        font-size: rem(17px);
        white-space: nowrap;
        margin-right: rem(6px);
    }
}

.catalog-header__icon {
    width: rem(18px);
    height: rem(18px);
}