.filter__dropdown-wrap {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.filter__circle {
    flex: 0 0 16.6666%;
    display: flex;
}

.filter__color-input {
    display: none;
}

.filter__color-input:checked+.filter__label::before {
    border: 3px solid $brown;
}

.filter__label {
    font-size: rem(16px);
    display: inline-flex;
    line-height: 140%;
    align-items: center;
    position: relative;
    cursor: pointer;

    &::before {
        content: "";
        flex: 0 0 rem(22px);
        align-self: flex-start;
        height: rem(22px);
        background-color: #fff;
        border-radius: 50%;
        margin-right: rem(10px);
        border: 3px solid transparent;
    }

    &--1::before {
        background-color: #000;
    }

    &--2::before {
        background-color: rgb(5, 17, 121);
    }

    &--3::before {
        background-color: rgb(107, 37, 52);
    }

    &--4::before {
        background-color: rgb(68, 109, 45);
    }
    &--5::before {
        background-color: rgb(219, 38, 98);
    }
    &--6::before {
        background-color: rgb(51, 55, 58);
    }
    &--7::before {
        background-color: rgb(176, 54, 187);
    }
}

.filter__dropdown {
    display: none;
}
