.filter__item {
    flex: 0 0 50%;
}

.filter__link {
    cursor: pointer;
    margin-bottom: rem(15px);
    display: block;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: rem(15px);
    border-bottom: 1px solid rgba(0, 0, 0, 0.15);

    span {
        font-weight: 500;
        font-size: rem(17px);
        line-height: rem(30px);
        display: flex;
    }

    &:hover {
        transition: all .2s ease;
        color: $brown;

        .arrow {
            transition: all .2s ease;
            fill: $brown;
        }
    }
}

.filter__dropdown-wrap {
    min-height: rem(110px);
    align-items: center;
}


.filter__dropdown {
    display: none;

}

.filter__price-container {
    width: 100%;
}

.filter__price-container .ui-slider {
    height: 2px;
    background: rgba(0, 0, 0, 0.15);
    border: none;
    position: relative;
}

.filter__price-container .ui-slider .ui-slider-handle {
    position: absolute;
    margin-left: rem(-16px);
    width: 20px;
    height: 20px;
    cursor: pointer;
    top: -11px;
    background: $brown;
    z-index: 2;
    transition: transform 0.2s ease;
    outline: none;

    &::before {
        content: "";
        height: 12px;
        width: 2px;
        background-color: #fff;
        position: absolute;
        top: 3px;
        left: 6px;
    }

    &::after {
        content: "";
        height: 12px;
        width: 2px;
        background-color: #fff;
        position: absolute;
        top: 3px;
        right: 6px;
    }
}

.ui-state-active {
    border: none !important;
    border: 1px solid #c5c5c5 !important;
}

.filter__price-container .ui-slider .ui-slider-range {
    position: absolute;
    z-index: 1;
    height: 2px;
    border: 0;
    background: #000;
    border-radius: 10px;
}

.filter-left,
.filter-right {
    height: rem(32px);
    background: #FFF;
    border-radius: rem(28px);
    border: rem(2px) solid $brown;
    text-align: center;
    font-size: rem(18px);
    padding: rem(16px) rem(10px);
    width: rem(80px);
    margin-top: rem(30px);
}

.filter-left {
    float: left;
}

.filter-right {
    float: right;
}