.header__contacts {
    display: flex;
    justify-content: space-between;
    padding: rem(8px) 0;
    border-bottom: rem(1px) solid rgba(0, 0, 0, 0.15);

    @include tablets() {
        display: none;
    }
}

.address {
    font-size: rem(17px);
    display: flex;

    &__item {
        margin-right: rem(33px);

        &:last-child {
            margin-right: 0;
        }
    }
}

.address__link {
    color: rgba(0, 0, 0, 0.4);
}

.address__link span {
    color: #000;
    transition: all .2s ease;
}

.address__link:hover span {
    color: $brown;
}

.socials {
    display: flex;
    align-items: center;

    &__item {
        margin-right: rem(15px);

        &:last-child {
            margin-right: 0;
        }
    }

    &__link {
        display: flex;
        width: rem(14px);
        height: rem(14px);

        &:hover .socials__icon {
            transition: all .2s ease;
            fill: $brown;
        }
    }

    &__icon {
        width: 100%;
        height: 100%;
        fill: rgba(0, 0, 0, 0.4);
    }
}
