.logo {
    margin-right: rem(15px);

    @include tablets() {
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.logo__img {

    @include tablets() {
        height: rem(22px);
    }

}

.header__navigation {
    padding: rem(20px) 0 rem(30px) 0;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include tablets() {
        padding: rem(13px) 0;

    }
}

.navigation {
    font-weight: 500;
    font-size: rem(20px);
    transition: transform .5s ease;

    &__link:hover {
        transition: all .2s ease;
        color: $brown;
        fill: $brown;
    }
}

.navigation__link:hover .arrow {
    transition: all .2s ease;
    fill: $brown;
}

.navigation__list {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.navigation__item {
    margin-right: rem(30px);

    &:last-child {
        margin-right: 0;
    }
}

.header__nav {
    @include tablets() {
        overflow: auto;
        position: fixed;
        top: 0;
        right: 0;
        height: 100%;
        background-color: #fff;
        z-index: 100000;
        left: 0;
        transform: translateX(-100%);
        margin-top: rem(48px);
        padding-bottom: rem(60px);
    }
}

.header__nav-list {
    margin-right: rem(15px);

    @include tablets() {
        flex-direction: column;
        align-items: flex-start;
        width: 95%;
        max-width: rem(1200px);
        margin: 0 auto;
    }
}

.header__nav-item {
    @include tablets() {
        margin-right: 0;
        padding: rem(18px) 0;
        border-bottom: 1px solid rgba(0, 0, 0, 0.15);
        width: 100%;
    }
}

.navigation-active {
    @include tablets() {
        transform: translateX(0%);
    }
}

.dropdown:hover {
    fill: $brown;
}

.arrow {
    width: rem(10px);
    height: rem(10px);
    fill: #000;

    &:hover {
        fill: $brown;
    }
}