.desc-card__wishlist {
    font-size: rem(17px);
    line-height: rem(20px);
    color: $brown;
    margin-bottom: rem(3px);
    transition: all .3s ease;

    &:hover {
        color: $darkBrown;
    }
}

.desc-card__title{
    font-weight: 500;
    font-size: rem(32px);
    line-height: rem(40px);
    margin-bottom: rem(20px);
}

.rating__review {
    font-size: rem(17px);
    line-height: rem(20px);
    color: rgba(0, 0, 0, 0.4);
    margin-left: rem(5px);
}

.desc-card__rating {
    display: flex;
    align-items: center;
}

.desc-card__price {
    margin-top: rem(17px);
    margin-bottom: rem(21px);
}

.desc-card__price-discount {
    font-size: rem(32px);
} 

.desc-card__price-actual {
    font-size: rem(44px);
    line-height: rem(53px);
}

.desc-card__text {
    line-height: rem(26px);
    color: rgba(0, 0, 0, 0.4);
    margin-bottom: rem(26px);
    display: block;
}