.catalog-content__body {
    display: flex;
    position: relative;
}

.aside {
    flex: 0 0 25%;

    @include tablets() {
        display: none;
        position: absolute;
        top: rem(-40px);
        right: 0;
        background-color: #fff;
        z-index: 100;
        min-width: rem(280px);
        padding: rem(10px);
        border: 1px solid rgba(0, 0, 0, .15);
    }
}

.active-aside {

    @include tablets() {
        transition: all .3s ease;
        opacity: 1;
        visibility: visible;
    }
}

.catalog {
    flex: 0 0 75%;
    min-width: 0;

    @include tablets() {
        flex: 1;
    }
}

.filter__arrow {
    width: rem(10px);
    height: rem(10px);
}


.product__item--catalog {
    margin-bottom: 2.27273rem;
    min-width: 0;
    margin-left: 0;
    margin-right: 0;
    width: 32.222% !important;

    @include phones() {
        width: 100% !important;
        max-width: 100%;
    }
}

.catalog__product .slick-slide>div {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
}